.toggle-mnu {
float: right;
color: #000;
position: absolute;
right: 10px;
top: 0;
text-align: center;
line-height: 48px;
display: block;
width: 28px;
height: 28px;
margin-top: 20px;
margin-right: 15px;
}
.toggle-mnu span:after, .toggle-mnu span:before {
content: "";
position: absolute;
left: 0;
top: 9px;
}
.toggle-mnu span:after {
top: 18px;
}
.toggle-mnu span {
position: relative;
display: block;
}
.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
width: 100%;
height: 2px;
background-color: #fff;
transition: all .3s;
-webkit-backface-visibility: hidden;
backface-visibility: hidden;
border-radius: 2px;
}
.toggle-mnu.on span {
background-color: transparent;
}
.toggle-mnu.on span:before {
transform: rotate(45deg) translate(-1px, 0);
}
.toggle-mnu.on span:after {
transform: rotate(-45deg) translate(6px, -7px);
}
.toggle-mnu.on + #menu {
opacity: 1;
visibility: visible;
}
